import React, { useState } from 'react';
import { Box } from '@mui/system';
import { styled, useTheme, createTheme, ThemeProvider } from '@mui/material/styles';
import { CssBaseline } from '@mui/material';

import Sidebar from '../sidebar/sidebar';
import Editor from '../editor/editor';

const drawerWidth = 240;

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
        flexGrow: 1,
        padding: theme.spacing(3),
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        marginLeft: `-${drawerWidth}px`,
        ...(open && {
            transition: theme.transitions.create('margin', {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
            marginLeft: 0,
        }),
    }),
);

const App = () => {
    const [folders, setFolder] = useState([{ id: 2, title: '–®–∫–æ–ª–∞', text: '<p>Hello World! üåéÔ∏è</p>' }, { id: 3, title: '–õ—ñ—Ç–µ—Ä–∞—Ç—É—Ä–∞', text: '<h3>Literature!</h3>' }]);
    const [activeFile, setActiveFile] = useState(1);
    const [open, setOpen] = useState(false);

    const handleDrawerOpen = () => {
        setOpen(!open);
    };
    
      const handleDrawerClose = () => {
        setOpen(false);
    };

    const getActiveFile = () => {
        // console.log(folders.find(folder => folder.id === activeFolder));
        if(folders.find(folder => folder.id === activeFile)) {
            return folders.find(folder => folder.id === activeFile)
        }   else {
            return false;
        }
    }

    const onUpdateNote = (updatedNote) => {
        const updatedFoldersArr = folders.map(folder => {
            if(folder.id === updatedNote.id) {
                return updatedNote;
            }
            return folder;
        });
        setFolder(updatedFoldersArr);
    }

    return (
        <>
            <Box sx={{ display: 'flex' }}>
                <CssBaseline/>
                <Sidebar folders={folders} setActiveFile={setActiveFile} open={open} handleDrawerOpen={handleDrawerOpen} handleDrawerClose={handleDrawerClose} />
                <Editor  activeFile={getActiveFile()} onUpdateNote={onUpdateNote} open={open} />
                
            </Box>
        </>
    )
}

export default App;